/* *****************************************************

    ** Custom Stylesheet **

    Any custom styling you want to apply should be
    defined here.

***************************************************** */
*,
*:after,
*::before {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
}


.gn-menu-main,
.gn-menu-main ul {
		margin: 0;
		padding: 0;
		background: whitesmoke;
		color: #5f6f81;
		list-style: none;
		text-transform: none;
		font-weight: 300;
		font-family: 'Lato', Arial, sans-serif;
		line-height: 60px;
		width: 340px;
}

.gn-menu-main {
		width: 100%;
		height: 65px;
		font-size: 13px;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}

.gn-menu a {
		display: block;
		height: 100%;
		color: #6CABDA;
		text-decoration: none;
		font-size: 20px;
		cursor: pointer;
}
.gn-menu a:hover {
	background-color: #6CABDA;
    color:white!important;
    cursor: pointer;
}
#gn-menu > li:hover {
    background-color: #6CABDA;
    color:white!important;
    cursor: pointer;
}

.gn-menu-main > li {
		display: block;
        width: 80px;
		float: left;
		height: 100%;
		border-right: 1px solid #c6d0da;
		text-align: center;
}

/* icon-only trigger (menu item) */

.gn-menu-main li.gn-trigger {
		width: 80px;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
}


.gn-menu-main > li.right {
		float: right;
		border-right: none;
		border-left: 1px solid #c6d0da;
}
.gn-menu-main > li > a {
		text-transform: uppercase;
		letter-spacing: 1px;
		font-weight: bold;
}

.gn-menu-main:after {
		display: table;
		clear: both;
		content: "";
}

.gn-menu-wrapper {
		position: fixed;
		top: 65px;
		bottom: 0;
		left: 0;
		z-index: 2000;
		overflow: hidden;
		width: 80px;
		border-top: 1px solid #c6d0da;
		background: whitesmoke;
		-webkit-transform: translateX(-80px);
		-moz-transform: translateX(-80px);
		transform: translateX(-80px);
		-webkit-transition: -webkit-transform 0.3s, width 0.3s;
		-moz-transition: -moz-transform 0.3s, width 0.3s;
		transition: transform 0.3s, width 0.3s;
}

.gn-scroller {
		position: absolute;
		overflow-y: scroll;
		width: 370px;
		height: 100%;
}

.gn-menu {
		border-bottom: 1px solid #c6d0da;
		text-align: left;
		font-size: 18px;
}

.gn-menu li:not(:first-child),
.gn-menu li li {
		box-shadow: inset 0 1px #c6d0da
}

.gn-submenu li {
		overflow: hidden;
		height: 0;
		-webkit-transition: height 0.3s;
		-moz-transition: height 0.3s;
		transition: height 0.3s;
		box-shadow: none!important;
}

.gn-submenu li a {
	color: #c1c9d1;
	padding-left: 60px;
	font-size: 16px;
}
.gn-submenu li a.online {
	color: #6FDE86;
}
.gn-submenu li a.offline {
	color: #FF6E2E;
}
input.gn-search {
		position: relative;
		z-index: 10;
		padding-left: 60px;
		outline: none;
		border: none;
		background: transparent;
		color: #5f6f81;
		font-weight: 300;
		font-family: 'Lato', Arial, sans-serif;
		cursor: pointer;
}

/* placeholder */

.gn-search::-webkit-input-placeholder {
		color: #5f6f81
}

.gn-search:-moz-placeholder {
		color: #5f6f81
}

.gn-search::-moz-placeholder {
		color: #5f6f81
}

.gn-search:-ms-input-placeholder {
		color: #5f6f81
}

/* hide placeholder when active in Chrome */

.gn-search:focus::-webkit-input-placeholder,
.gn-menu li.gn-search-item:hover .gn-search:focus::-webkit-input-placeholder {
		color: transparent
}

input.gn-search:focus {
		cursor: text
}

.gn-menu li.gn-search-item:hover input.gn-search {
		color: white
}

/* placeholder */

.gn-menu li.gn-search-item:hover .gn-search::-webkit-input-placeholder {
		color: white
}

.gn-menu li.gn-search-item:hover .gn-search:-moz-placeholder {
		color: white
}

.gn-menu li.gn-search-item:hover .gn-search::-moz-placeholder {
		color: white
}

.gn-menu li.gn-search-item:hover .gn-search:-ms-input-placeholder {
		color: white
}

.gn-menu-main a.gn-icon-search {
		position: absolute;
		top: 0;
		left: 0;
		height: 60px;
}

.gn-icon::before {
		display: inline-block;
		width: 60px;
		text-align: center;
		text-transform: none;
		font-weight: normal;
		font-style: normal;
		font-variant: normal;
		line-height: 1;
		speak: none;
		-webkit-font-smoothing: antialiased;
}

.gn-icon-help::before {
		content: "\e000"
}

.gn-icon-earth::before {
		content: "\e004"
}

.gn-icon-cog::before {
		content: "\e006"
}

.gn-icon-search::before {
		content: "\e005"
}

.gn-icon-download::before {
		content: "\e007"
}

.gn-icon-photoshop::before {
		content: "\e001"
}

.gn-icon-illustrator::before {
		content: "\e002"
}

.gn-icon-archive::before {
		content: "\e00d"
}

.gn-icon-article::before {
		content: "\e003"
}

.gn-icon-pictures::before {
		content: "\e008"
}

.gn-icon-videos::before {
		content: "\e009"
}

/* if an icon anchor has a span, hide the span */

.gn-icon span {
		width: 0;
		height: 0;
		display: block;
		overflow: hidden;
}

 .gn-icon-menu::before {
		vertical-align: -2px;
		width: 30px;
		height: 3px;
		background: #5f6f81;
		box-shadow: 0 3px white, 0 -6px #5f6f81, 0 -9px white, 0 -12px #5f6f81;
		content: '';
}
#gn-menu > li.gn-trigger:hover > .gn-icon-menu::before {
    	background: white;
		box-shadow: 0 3px #6CABDA, 0 -6px white, 0 -9px #6BABDA, 0 -12px white;
}

.gn-icon-menu.gn-selected::before {
		background: #5993cd;
		box-shadow: 0 3px white, 0 -6px #5993cd, 0 -9px white, 0 -12px #5993cd;
}

/* styles for opening menu */

.gn-menu-wrapper.gn-open-all,
.gn-menu-wrapper.gn-open-part {
		-webkit-transform: translateX(0px);
		-moz-transform: translateX(0px);
		transform: translateX(0px);
}

.gn-menu-wrapper.gn-open-all {
		width: 340px
}

.gn-menu-wrapper.gn-open-all .gn-submenu li {
		height: 60px
}

@media screen and (max-width: 422px) { 
	.gn-menu-wrapper.gn-open-all {
			-webkit-transform: translateX(0px);
			-moz-transform: translateX(0px);
			transform: translateX(0px);
			width: 100%;
	}

	.gn-menu-wrapper.gn-open-all .gn-scroller {
			width: 130%
	}
}

#gn-menu > li.gn-trigger > div > div > ul > li [class^="fa"] {
	margin: 22px;
    font-size: 32px;
}
a.add-new {
    color: #3CC051;
    font-size: 34px;
}
#gn-menu > li:hover > a {
    color: white;
}
ul.gn-submenu > li > a.more {
	font-size: 30px;
	padding-right: 10px;
	padding-left: 0;
	color: #32465A;
	line-height: 10px;
}
.gn-menu-wrapper.gn-open-all .gn-submenu li.smaller {
    height: 30px;
    text-align: center;
}

.header {
    border: 0 none;
    box-shadow: none;
    /*height: 42px;*/
    margin: 0;
    min-height: 42px;
    padding: 10px 0px 10px 0px;
    width: 100%;
    border-bottom: 1px solid #e5e6ef;

}
.header.navbar-fixed-top {
    z-index: 9995 !important;
}
.header .navbar-logo {
    display: inline-block;
    margin-right: 0;
    margin-top: -1px;
    padding: 0px !important;
}
.header .navbar-logo img {
    margin-left: 20px;
}
.header .navbar-toggle {
    background-image: none;
    border: 0 none;
    box-shadow: none;
    color: #fff;
    filter: none;
    margin: 25px 6px 4px;
    padding: 2px 0 6px;
}
.header .navbar-toggle:hover {
    background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
    text-decoration: none;
}
.header .navbar-nav {
    display: block;
    /*margin-right: 20px;*/
}
.header .navbar-nav > li {
    margin: 0;
    padding: 0;
    display: inline-block;
}
.header .navbar-nav > li.dropdown, .header .navbar-nav > li.dropdown > a {
    padding-left: 4px;
    padding-right: 4px;
}
.header .navbar-nav > li.dropdown > a:last-child {
    padding-right: 0;
}
.header .navbar-nav > li.dropdown:last-child {
    padding-right: 2px;
}
.header .navbar-nav > li.dropdown .dropdown-toggle {
    margin: 0;
    padding: 15px 10px 0px;
}
.header .navbar-nav > li.dropdown .dropdown-toggle > i {
    font-size: 26px; color:#d5d7e3;
}
.header .navbar-nav > li.dropdown .dropdown-menu > li > a > i {
    font-size: 14px;
}
.header .navbar-nav > li.dropdown.user .dropdown-toggle {
    padding: 7px 4px 6px 9px;
}
.header .navbar-nav > li.dropdown.user .dropdown-toggle:hover {
    text-decoration: none;
}
.header .navbar-nav > li.dropdown.user .dropdown-toggle .username {
    color: #ddd;
}
.header .navbar-nav li.dropdown.user .dropdown-toggle i {
    display: inline-block;
    font-size: 16px;
    margin: 0;
}
.header .navbar-nav > li.dropdown.user .dropdown-menu i {
    display: inline-block;
    width: 15px;
}
.header .navbar-nav > li.dropdown .dropdown-toggle .badge {
    position: absolute;
    right: 20px;
    top: 8px;
}
.header .navbar-nav > li.dropdown.language {
    margin: 0;
    padding-left: 0;
    padding-right: 0;
}
.header .navbar-nav > li.dropdown.language > a {
    color: #ddd;
    font-size: 13px;
    padding: 10px 1px 12px 5px;
}
.header .navbar-nav > li.dropdown.language > a > img {
    margin-bottom: 2px;
}
.header .navbar-nav > li.dropdown.language > a > i {
    font-size: 16px;
}
.header .navbar-nav > li.dropdown.language > .dropdown-menu > li > a > img {
    margin-bottom: 2px;
}
.header .navbar-nav .dropdown-menu {
    margin-top: 3px;
}
.border-top {
        border-top: 1px solid #ccc!important;
}
@media only screen and (max-width : 768px) {
 .m10 {
 margin: 10px;
}
 .header .navbar-brand {
 margin-top: 15px !important;
}
}
.navbar {
    position: static;
}
